





























import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import axios from 'axios';

@Component
export default class ImageInput extends Vue {
    @Prop({ type: Object }) value!: object;
    private errorDialog!: boolean;
    private errorText: string = '';
    private uploadFieldName: string = 'file';
    private maxSize: number = 1024;

    launchFilePicker() {
        //this.$refs.file;
        //this.$refs.files.click();
    }

    onFileChange(fieldName: string, files: any[]) {
        var imageFile = files[0];

        if (files.length > 0) {
            var size = imageFile.size / this.maxSize;
            if (!imageFile.type.match('image.*')) {
                this.errorDialog = true;
                this.errorText = 'Пожалуйста, выберите изображение';
            }
            else if (size > 1) {
                this.errorDialog = true;
                this.errorText = 'Ваш файл слишком большой, выберите изображение менее ' + this.maxSize / 1024 + ' Мб';
            }
            else {
                var formData = new FormData();
                var imageURL = URL.createObjectURL(imageFile);
                formData.append(fieldName, imageFile);

                this.$emit('input', { formData, imageURL });
            }
        }
    }
}
